                                config: {
                                    options: [],
                                },
                                validationRules: {
                                    //"required": true
                                },
                            },

                        ]
                    }
                ],
                buttons: [
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-copy pr-2'></i>Copy Last Week",
                        isVisible: true,
                    },
                    {
                        type: "submit",
                        class: "btn btn-warning formbtn mr-2 text-dark",
                        text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                        isVisible: true,
                    },
                    {
                        type: "submit",
                        class: "btn btn-success text-white formbtn mr-2",
                        text: "<i class='fa fa-save pr-2'></i>Send For Approval",
                        isVisible: true,
                    }
                ],
                listActions: [
                    {
                        title: 'Delete',
                        icon: 'fa fa-trash',
                        callbackfunction: 'DeleteData',
                        additionalSpan: '',
                        isVisible: true
                    },
                ],
                LegendArray: [{
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }, {
                    class: '',
                    backgroundColor: 'rgb(245 234 222)',
                    name: 'Recurring Tickets'
                }
                ],
                DataLoaded: 0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                noRecord: false,
                TotalRecords: 0,
                isLoading: false,
                TimesheetList: [],
                UserId: null,
                Headers: [
                    //     {
                    //     "COLUMN_NAME": "ProjectName",
                    //     "DATA_TYPE": "string",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Projects",
                    //     "DISPLAY_ORDER": 1
                    // },
                    // {
                    //     "COLUMN_NAME": "break_tt",
                    //     "DATA_TYPE": "text",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Mon, Dec",
                    //     "DISPLAY_ORDER": 2,
                    // },
                ],
            }
        },
        created: async function () {
            this.DataLoaded = 1;
            await this.GetUsersForTimesheet();
            var VueObj = this;
            VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
            VueObj.FetchData();
        },
        mounted: function () {
            var VueObj = this;
            // VueObj.RdatePickerForWeekRange('week-picker');
            VueObj.RdatePickerForWeekRange('date-range');
            console.log('mounted')
        },
        methods: {
            async GetUsersForTimesheet() {
                var vm = this;
                vm.isLoading = true;
                var url = `companyId=270936&loggedUserId=769083&userType=CA&userId=769083&moduleName=`;
                await DataService.GetUsersForTimesheet(url)
                    .then(function (response) {

                        response.data.result.forEach(item => {
                            vm.obj = {
                                name: item.username,
                                value: item.useR_ID
                            }
                            vm.FormSchema[0].Data[1].config.options.push(vm.obj);
                        });

                        vm.isLoading = false;
                    });
            },
            OnChange: function () {
                var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
                if (checkCount > 0) {
                    $('#DeleteMultiple').removeClass('disabled');
                }
                else {
                    $('#DeleteMultiple').addClass('disabled');
                }
            },
            commonHeaderButtonClick: function (event) {


            },
            async onSubmit(formfields, e) {
                var vm = this;
                vm.FetchData();
            },
            async GetStartEndDates(dateSpan) {
                GetStartEndDate_New(dateSpan, null)
                // $.ajax({
                //     url: '@Url.Action("GetStartEndDate_New", "BiReport")',
                //     data: { DateSpan: dateSpan.toLowerCase(), report: $('#hdnGroupBy').val() },
                //     type: 'POST',
                //     async: true,
                //     success: function (data) {
                //         $('#txtFromDate').val(data.dateStart);
                //         $('#txtToDate').val(data.dateEnd);

                //         $('#aDateLable').html('').html(data.displayText);
                //         hideLoader();

                //     },
                //     error: function () { hideLoader(); }
                // });
            },
            async GetStartEndDate_New(DateSpan, report) {
 
            },
            async RdatePickerForWeekRange(dateFrom) {
                //var cur = Globalize.culture();
                var cDate = new Date();
                var whoseEvent = "";
                var newDate = "";
                $("#" + dateFrom).keydown(function (event) {
                    whoseEvent = "StartDate";
                    event.preventDefault();
                });

                var globalDateFormat = 'dd/MM/YYYY';
                var localDateFormat = 'dd/MM/YYYY';


                var $DivFrom = $("<div>").addClass("input-group-append float-right").attr({ "data-target": "#" + dateFrom, "data-toggle": "datetimepicker" });
                var $DivFromSpan = $("<span>").addClass("input-group-text").append('<i class="fa fa-calendar"></i>');
                $DivFrom.append($DivFromSpan);
                $DivFrom.insertAfter($('#' + dateFrom));


                // $('#' + dateFrom).datetimepicker({
                //     format: 'L',
                //     //locale: cur.name,
                //     showClear: true,
                //     showClose: true

                // })

                $('#' + dateFrom).on("change.datetimepicker", function (e) {
                    format: 'dd/mm/yyyy'
                    var offset = 1;
                    var weekStartDay = ('@weekStart').toLowerCase();
                    switch (weekStartDay) {
                        case 'tuesday':
                            offset = 2;
                            break;
                        case 'wednesday':
                            offset = 3;
                            break;
                        case 'thursday':
                            offset = 4;
                            break;
                        case 'friday':
                            offset = 5;
                            break;
                        case 'satureday':
                            offset = 6;
                            break;
                        case 'sunday':
                            offset = 0;
                            break;
                        default:
                        // code block
                    }

                    var today = new Date(e.date);
                    var currentDay = today.getDay() - offset;
                    var startdate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay);
                    var enddate = new Date(today.getFullYear(), today.getMonth(), today.getDate() - currentDay + 6);
                    var dateTime = moment(startdate).format('L') + '-' + moment(enddate).format('L');
                    //alert(dateTime);
                    $('#txtFromDate').val(moment(startdate).format('L'));
                    $('#txtToDate').val(moment(enddate).format('L'));
                    $('#aDateLable').text(dateTime);
                    $('#' + dateFrom).datetimepicker('autochange');
                    //$('#' + dateTo).datetimepicker('minDate', e.date);
                });
            },
            FetchData: async function () {
                var VueObj = this;
                VueObj.isLoading = true;
                var fDate = VueObj.$options.filters.formatDateTime(VueObj.FormSchema[0].Data[0].value)
                var tDate = VueObj.$options.filters.formatDateTime(VueObj.FormSchema[0].Data[0].value)
                // var url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&userId=${VueObj.FormSchema[0].Data[1].value}&companyId=270936`;
                var url = `companyId=270936&userId=${VueObj.FormSchema[0].Data[1].value}&dateFrom=${fDate}&dateTo=${tDate}`;
                await DataService.ListTimesheetForWeek(url).then(response => {
                    if (response.data != null && response.data != "" && response.data.length > 0) {
                        // const headerList=Object.keys(response.data[0]).filter(key => key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail');

                        const headerList = Object.keys(response.data[0]);

                        VueObj.isLoading = false;
                        var count;
                        headerList.forEach(item => {
                            count++;

                            if (item == "ProjectId" || item == "TaskId") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": false,
                                    "DISPLAY_NAME": item,
                                    "DISPLAY_ORDER": count,
                                });
                            }
                            else if (item == "TaskDetail") {
                                // this.Headers.push({
                                //     "COLUMN_NAME": item,
                                //     "DATA_TYPE": "string",
                                //     "SORTABLE": false,
                                //     "VISIBLE": true,
                                //     "DISPLAY_NAME": item,
                                //     "DISPLAY_ORDER": count,
                                // });
                            }
                            else if (item == "ProjectName") {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "string",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": "Projects",
                                    "DISPLAY_ORDER": 1
                                });

                            }
                            else {
                                this.Headers.push({
                                    "COLUMN_NAME": item,
                                    "DATA_TYPE": "text",
                                    "SORTABLE": false,
                                    "VISIBLE": true,
                                    "DISPLAY_NAME": getPreviousDay(item),
                                    "DISPLAY_ORDER": count,
                                });
                            }
                        });

                        // headerList.forEach((item) => {
                        //     VueObj.Headers.forEach(column => {
                        //         column.settings = null;

                        //         if (column.COLUMN_NAME == item && column.COLUMN_NAME !=='ProjectName' && item !=='ProjectName') {
                        //             column.settings = {
                        //                 //isInSlot: true
                        //             }
                        //         }
                        //     }
                        //     );
                        // });


                        VueObj.Headers.forEach((column) => {
                            column.settings = null;
                            headerList.forEach(item => {

                                if (column.COLUMN_NAME == item && column.COLUMN_NAME !== 'ProjectName' && column.COLUMN_NAME !== 'TaskDetail') {
                                    column.settings = {
                                        isInSlot: true
                                    }
                                }
                            }
                            );
                        });


                        VueObj.TotalRecords = response.data.length;
                        VueObj.TimesheetList = response.data;

                        VueObj.TotalPages = 1;
                        VueObj.CurrentPage = 1;
                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;

                        $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                        $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                        VueObj.noRecord = false;
                    }
                    else {
                        VueObj.noRecord = true;
                        VueObj.TimesheetList = [];
                    }

                });
            },
            SingleDeleteData: function (item) {
                var vm = this;
                var fDate = vm.$options.filters.formatDateTime(vm.FormSchema[0].Data[0].value)
                var tDate = vm.$options.filters.formatDateTime(vm.FormSchema[0].Data[0].value)
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("projectId")), function () {
                    let url = `companyId=270936&userId=${vm.FormSchema[0].Data[1].value}&projectId=${item.ProjectId}&taskId=${item.TaskId}&dateFrom=${fDate}&dateTo=${tDate}`;
                    DataService.DeleteTrackEntryByProjectAndTaskId(url).then(response => {
                        debugger
                        if (response.data == '1') {
                            vm.ShowAlert(vm.$t('DeletedSuccessfully'), "Success", true, vm.$t('Alert'));

                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('val')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            },
            actionButtonClick(callbackfunction, item) {
                var vm = this;
                switch (callbackfunction) {
                    case 'Editdata':
                        this.EditAssignCategoryClick(item)
                        break;
                    case 'DeleteData':
                        vm.SingleDeleteData(item)
                        break;
                }
            },
            renderActions: function (row) {
                var self = this;
                let rtnActionArray = [];
                self.listActions.forEach(actionItem => {
                    let action = {
                        ...actionItem
                    };
                    switch (action.title) {
                        case 'Edit':
                            action.isVisible = true;
                            break;
                        case 'Delete':
                            action.isVisible = true;
                            break;
                        default:
                            action.isVisible = true;
                            break;
                    }
                    rtnActionArray.push(action);
                });
                return rtnActionArray;
            }
        }
